@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

// @mixin block-to-center {
//   display: block;
//   margin: 0 auto;
// }

@mixin title {
  margin: 0;
  font-family: "Cormorant Garamond", "Tahoma", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: calc(44 / 34);
  letter-spacing: 0.5px;
  text-align: center;
}

@mixin title-extension {
  display: block;
  margin: 0 auto;
  text-align: center;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $decor-1;
    left: 0;
    bottom: -6px;
  }
}

